<template>
  <div class="home">
    <Navbar></Navbar>
    <div v-for="book in books">

    </div>
  </div>
</template>

<script>
  import Navbar from './Navbar'
  export default {
    name: 'Home',
    data() {
      return {

      }
    },
    mounted() {
      this.$store.dispatch('getBooks')
    },
    computed: {
      books() {
        return this.$store.state.books
      }
    },
    components: {
      Navbar
    }
  }
</script>

<style scoped>
</style>


