<template>
    <div class="navbar">
        <h1 class="title">My&#8226;brary</h1>
        <div class="navRight">
            <div v-if="!user.name">
                <button class="btn btn-light" @click=goToLogin>Login / Create Account</button>
            </div>
            <div v-if="user.name">
                <button class="btn btn-light">Add a Book</button>
                <button class="btn btn-light">Profile</button>
                <button class="btn btn-light" @click=logout(user)>Logout</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Navbar',
        data() {
            return {

            }
        },
        mounted() {
            this.$store.dispatch('authCheck')
        },
        methods:{
            logout(user){
                this.$store.dispatch('logout', user)
            },
            goToLogin(){
                this.$store.dispatch('goToLogin')
            }
        },
        computed: {
            user() {
                return this.$store.state.user
            }
        }
    }
</script>

<style scoped>
    .navbar {
        background-color: indigo;
        height: 10vh;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    .title {
        color: white;
    }

    .navRight {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: baseline;
    }

    button {
        margin: 10px;
    }
</style>


